"use client";

import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import clsx from "clsx";
import Navbar from "../components/Navbar";
import { Button } from "@/components/ui/button";

export default function QueryHome() {
  const router = useRouter();

  const categories = [
    { id: "Road Sign", label: "Road Sign" },
    { id: "Road Marking", label: "Road Marking" },
    { id: "Traffic Calming Measures", label: "Traffic Calming" },
    { id: "Custom Query", label: "Custom Query" },
  ];

  return (
    <div className="min-h-screen bg-background text-foreground flex flex-col">
      <Navbar />

      {/* Centered Hero */}
      <div className="flex-1 flex flex-col justify-center items-center px-6">
        <h1 className="text-3xl font-bold mb-6 text-center">
          Hi, how can I help you today?
        </h1>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 w-full max-w-2xl">
          {categories.map((cat) => (
            <button
              key={cat.id}
              onClick={() => {
                if (cat.id === "Custom Query") {
                  router.push("/query-builder");
                } else {
                  router.push(`/query/category?c=${encodeURIComponent(cat.id)}`);
                }
              }}
              className={clsx(
                "py-6 rounded-xl border text-lg font-medium transition",
                "bg-card hover:bg-accent hover:text-accent-foreground",
                "dark:bg-zinc-900 dark:hover:bg-zinc-800"
              )}
            >
              {cat.label}
            </button>
          ))}
        </div>
      </div>
    </div>
  );
}
